<template>
  <div class="header">
    <el-row class="el-row-plus" type="flex" justify="center">
      <el-col :xs="0" :sm="6" :md="6" :lg="8" :xl="8">
        <div class="img">
          <el-image
            :src="require('@/assets/logo.png')"
            fit="contain"
          ></el-image>
        </div>
      </el-col>
      <el-col :xs="24" :sm="12" :md="10" :lg="8" :xl="8">
        <el-menu
          :default-active="this.$router.self"
          mode="horizontal"
          class="el-menu-plus"
          text-color="#84C1FF"
          active-text-color="#409EFF"
          router
          @select="select"
        >
          <el-menu-item index="/home" class="el-menu-item-plus"
            >首页</el-menu-item
          >
          <el-menu-item index="/plane" class="el-menu-item-plus"
            >飞机</el-menu-item
          >
          <el-menu-item index="/train" class="el-menu-item-plus"
            >火车</el-menu-item
          >
          <el-submenu index="/admin" class="el-menu-item-plus">
            <template slot="title">{{ account }}</template>
            <el-menu-item
              index="/self"
              class="el-menu-submenu-item-plus"
              :disabled="disableSelf"
              >个人主页</el-menu-item
            >
            <el-menu-item
              index="login"
              class="el-menu-submenu-item-plus"
              @click="login"
              >登录</el-menu-item
            >
            <el-menu-item
              index="register"
              class="el-menu-submenu-item-plus"
              @click="register"
              >注册</el-menu-item
            >
            <el-menu-item
              class="el-menu-submenu-item-plus"
              @click="logout"
              :disabled="disableLogout"
              >登出</el-menu-item
            >
          </el-submenu>
          <el-menu-item
            ><el-avatar :size="40" :src="avatarSrc"></el-avatar
          ></el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
    <el-dialog
      title="注册"
      :visible.sync="registerDialogVisible"
      append-to-body
      @closed="resetRegisterInfo"
    >
      <y-register></y-register>
    </el-dialog>

    <el-dialog
      title="登录"
      :visible.sync="loginDialogVisible"
      append-to-body
      @closed="resetLoginInfo"
    >
      <y-login></y-login>
    </el-dialog>

    <el-dialog
      title="找回密码"
      :visible.sync="foundDialogVisible"
      append-to-body
      @closed="backLogin"
    >
      <y-lost></y-lost>
    </el-dialog>
  </div>
</template>

<script>
import YRegister from "./YRegister.vue";
import YLogin from "./YLogin.vue";
import YLost from "./YLost.vue";
import { mapState } from "vuex";

export default {
  name: "YFooter",
  components: {
    YRegister,
    YLogin,
    YLost,
  },
  data() {
    return {
      account: "用户",
      avatarSrc: "",
      disableSelf: true,
      disableLogout: true,
      registerDialogVisible: false,
      loginDialogVisible: false,
      foundDialogVisible: false,
    };
  },
  methods: {
    select(idx, idxPath) {
      console.log(idx, idxPath);
    },
    login() {
      console.log(this);
      this.loginDialogVisible = !this.loginDialogVisible;
    },
    register() {
      this.registerDialogVisible = !this.registerDialogVisible;
    },
    logout() {
      this.account = "用户";
      this.avatarSrc = "";
      this.disableLogout = true;
      this.disableSelf = true;
      this.$store.state.login.loginState = false;
    },
    resetRegisterInfo() {
      this.$store.state.register.address.resetFields();
    },
    resetLoginInfo() {
      this.$store.state.login.address.resetFields();
    },
    backLogin() {
      this.loginDialogVisible = !this.loginDialogVisible;
    },
  },
  computed: {
    ...mapState("user", ["userForm"]),
    ...mapState("register", ["rDV"]),
    ...mapState("login", ["lDV", "fDV", "loginState"]),
  },
  watch: {
    rDV: {
      handler() {
        this.registerDialogVisible = !this.registerDialogVisible;
      },
    },
    lDV: {
      handler() {
        this.loginDialogVisible = !this.loginDialogVisible;
      },
    },
    fDV: {
      handler() {
        this.foundDialogVisible = !this.foundDialogVisible;
      },
    },
    loginState: {
      handler() {
        console.log("watch loginState");
        if (this.loginState == true) {
          this.disableLogout = false;
          this.disableSelf = false;
          let hAccount = localStorage.getItem("account");
          if (hAccount != "") {
            this.account = localStorage.getItem("account");
            this.avatarSrc = localStorage.getItem("avatarSrc");
          } else {
            this.account = this.userForm.usrAccount;
            this.avatarSrc = this.userForm.usrAvatar;
          }
        }
      },
    },
  },
  created() {
    let hAccount = localStorage.getItem("account");
    if (hAccount != null) {
      this.$store.state.login.loginState = true;
    }
  },
};
</script>

<style>
label {
  text-align: center !important;
}
.header {
  position: sticky;
  top: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.img {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.el-image-plus {
  width: 25%;
  height: 25%;
}
.el-row-plus {
  background-color: #fff;
}
.el-img-plus {
  background-color: #fff;
}
.el-menu-plus {
  display: flex;
  justify-content: flex-end;
}
.el-menu-item-plus {
  width: 20%;
  text-align: center;
}
.el-menu-item:hover {
  color: #409eff !important;
  background-color: #d6e9fe !important;
}
.el-menu--collapse .el-menu .el-submenu,
.el-menu--popup {
  min-width: 120px !important;
}
.el-menu-submenu-item-plus {
  min-width: 120px !important;
}

@media screen and (max-width: 768px) {
  .el-dialog {
    width: 350px !important;
  }
}
@media screen and (min-width: 768px) {
  .el-dialog {
    width: 600px !important;
  }
}
</style>
