<template>
  <div id="selfHelp">
    <el-row>
      <h2>头像修改/上传</h2>
      <el-divider></el-divider>
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        list-type="picture-card"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove"
      >
        <i class="el-icon-plus"></i>
        <div slot="tip" class="el-upload__tip">
          只能上传jpg/png文件，且不超过500kb
        </div>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-row>
    <el-row>
      <h2 style="margin-top: 20px">个人信息修改</h2>
      <el-divider></el-divider>
    </el-row>
    <el-row>
      <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
        <p>账号</p>
        <el-input size="mini" v-model="account" clearable class="el-input-plus">
        </el-input>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
        <p>密码</p>
        <el-input
          size="mini"
          v-model="password"
          clearable
          class="el-input-plus"
        >
        </el-input>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
        <p>电话</p>
        <el-input size="mini" v-model="phone" clearable class="el-input-plus">
        </el-input>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
        <p>邮箱</p>
        <el-input size="mini" v-model="email" clearable class="el-input-plus">
        </el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-button
        class="el-icon-edit"
        size="mini"
        type="primary"
        style="margin-top: 40px"
        >修改</el-button
      >
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      account: "",
      password: "",
      phone: "",
      email: "",
      dialogImageUrl: "",
      dialogVisible: false,
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
  },
};
</script>

<style scoped>
#selfHelp {
  padding: 30px 25px;
}
p {
  margin: 5px 0;
  font-size: 12px;
}
.el-input-plus {
  margin: 2px 0;
  max-width: 300px;
}
</style>
